import React from 'react';
import './App.css';
import HomePage from './pages/home-page/Hompage-componet';
import { Route, Switch } from 'react-router-dom';
import NavBar from './components/nav-bar/nav-bar';
import Shop from './pages/shop-page/shop';
import { SignInSignUpPage } from './pages/signIn-sign-up-page/signIn-sign-up';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentUser: null
    }
    console.log("🚀 ~ file: App.js ~ line 14 ~ App ~ constructor ~ props", props)
  }


  render() {
    return (
      <div className="App ">

        <div className='main'>
          <Route exact={false} path="/" component={NavBar} currentUser={this.state.currentUser} />
          <Route exact path="/clothes-shop-ReactProject" component={HomePage} />
          <Switch>
            <Route exact path="/clothes-shop-ReactProject/shop" component={Shop} />
            <Route exact path="/clothes-shop-ReactProject/logIn" component={SignInSignUpPage} />
          </Switch>
        </div>
      </div>
    );
  }
}

export default App;
